body, html {
    font-family: 'Oswald', sans-serif;
    flex: 1;
    border: 0;
    margin: 0;
    padding: 0;
}

.nav > div {
    margin-left: 24px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 24px;
}

.nav > nav {
    display: flex;
    width: 100%;
    height: 70px;
    background-color: #ccffcc;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 24px;
    align-content: center;
    align-items: center;
}

.nav > span {
    margin-left: 24px;
    font-size: 36px;
    color:  #09a76d;
}

.nav > nav > img {
    margin-left: 24px;
    margin-top: 5px;
    width: 60px;
    height: 60px;
}

.nav > nav > span {
    width: 100%;
    align-self: center;
    font-size: 30px;
    color:  #09a76d;
    margin-left: 300px;
}

.nav > nav > div {
    margin: 0px 10px;
}


.main {
    display: flex;
}



#mapid { 
    height: 100%;
    width: 95%;
}

#mapContainer {
    width: 90vw;
    height: 60vh;
    margin-left: 100px;
}

#containerCardsDestaque {
    display: flex;
    flex-direction: row;
    background-color:  #cee4de;
}

#cardPostoDestaque {
    display: flex;
    flex-direction: row;
    padding: 0px;
    width: 100%;
    height: 100px;
    margin-top: 24px;
    border: 2px solid black;
}

#conteudoCardDestaque{

    width: 300px;
    margin-right: 25px;
    background-color: #F8F9F9;
}

#cardPostoContainer {

    background-color:  #cee4de;
    display: flex;
    flex-direction: column;
    margin-top: 24px;
    width: 300px;
    height: 150px;
    padding-left: 24px; 
}

#containerLikes {
    align-self: flex-end;
    margin: 0;
    padding: 0;
}

#cardPostoContainer > button {
    background: none;
	color: inherit;
	border: none;
	padding: 0;
}


#containerLikes > button > img {
    
    height: 25px;
    width: 25px;
    
}

footer {
    margin-top: 24px;
    font-size: 12px;
    align-self: center;
}